  methods: {
    async getNewsHome() {
      let Resources = await this.$api.getNewsHome();
      this.leftNews = Resources.data.leftNews
      this.rightNews = Resources.data.rightNews
    }
  },
  mounted() {
    this.getNewsHome()
  }
}
</script>

